.btn {
  &,
  &:link,
  &:visited {
    text-decoration: none;
    font-size: 1.8rem;
    padding: 1.5rem 3rem;
    border-radius: 1.5rem;
    border: none;
    text-transform: uppercase;
    position: relative;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    will-change: transform;
    box-shadow: 0.2rem 0.6rem 1rem rgba(#333, 0.15);
  }

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0.2rem 1.2rem 1rem rgba(#333, 0.15);
  }

  &--color {
    background-color: #01c3ff;
    color: #fff;
  }

  &:active {
    transform: scale(0.97);
  }

  &:focus {
    outline: transparent;
  }
}
