.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  height: 100px;
}

.site-header {
  padding-top: 0 !important;
}

.footer-box {
  display: flex;
  justify-content: center;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1.5rem 3rem;
  border-radius: 1.5rem;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.2rem 0.6rem 1rem rgba(51, 51, 51, 0.15);
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0.2rem 1.2rem 1rem rgba(51, 51, 51, 0.15);
}
.btn--color {
  background-color: #01c3ff;
  color: #fff;
}
.btn:active {
  transform: scale(0.97);
}
.btn:focus {
  outline: transparent;
}

*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

.container {
  max-width: 114rem;
  margin: 0 auto;
}